$text_color: white;
@mixin box {
    display: block;
    width: 250 px;
    height: 200 px;
    background-color: blue; }
.block {
    background-color: black;
    h3 {
        color: $text_color; }
    &_color {
        background-color: red;
        button {
            @include box; } } }









